export default [{
  path: '/',
  name: "/",
  redirect: "/homepages",
  component: () => import("../components/PagesLayout"),
  children: [{
      path: 'homepages',
      name: "homepages",
      component: () => import("./views/Homepages"),
      meta: {
        keepAlive: true
      }
    },
    {
      path: 'classify',
      name: "classify",
      component: () => import("./views/Classify"),
      meta: {
        keepAlive: true
      }
    },
    {
      path: 'shopcar',
      name: "shopcar",
      component: () => import("./views/Shopcar"),
      meta: {
        keepAlive: true
      }
    },
    {
      path: 'personal',
      name: "personal",
      component: () => import("./views/Personal"),
      meta: {
        keepAlive: true
      }
    }
  ],
}];



