export default [{
  path: '/homepages',
  component: () => import("./Index"),
  children: [{
      path: 'recommend',
      name: 'recommend',
      component: () => import("./views/Recommend"),
    },
    {
      path: 'newhot',
      name: 'newhot',
      component: () => import("./views/Newhot"),
    },
    {
      path: 'integral',
      name: 'integral',
      component: () => import("./views/Integral"),
    },
    {
      path: 'notice',
      name: 'notice',
      component: () => import("./views/Notice"),
      
    },
    {
      path: 'notice/details',
      name: 'details',
      component: () => import("./views/Details"),
    }
  ]
}];



