export default [{
  path: '/personal',
  component: () => import("./Index"),
  children: [
    {
      path: 'myIntegral',
      name: 'myIntegral',
      component: () => import("./views/MyIntegral"),
    },
    {
      path: 'income',
      name: 'income',
      component: () => import("./views/Income"),
    },
    {
      path: 'myPurse',
      name: 'myPurse',
      component: () => import("./views/MyPurse"),
    },
    {
      path: 'generalize',
      name: 'generalize',
      component: () => import("./views/Generalize"),
    },
    {
      path: 'myOrders',
      name: 'myOrders',
      component: () => import("./views/MyOrders"),
    },
    {
      path: 'orderDetails',
      name: 'orderDetails',
      component: () => import("./views/orderDetails"),
    },
    {
      path: 'myTeam',
      name: 'myTeam',
      component: () => import("./views/MyTeam"),
    },
    {
      path: 'myAddress',
      name: 'myAddress',
      component: () => import("./views/MyAddress"),
    },
    {
      path: 'contact',
      name: 'contact',
      component: () => import("./views/Contact"),
    },
    {
      path: 'about',
      name: 'about',
      component: () => import("./views/About"),
    }
  ]
}];



