import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        count: 0,
        goodsoncart: 0
    },
    mutations: {
        increment (state) {
            state.goodsoncart++
        }
    },
    actions: {
        incrementAsync ({ commit }) {
            return new Promise((resolve, reject) => {
                setTimeout(() => {
                    commit('increment')
                    resolve()
                }, 3000)
            })
        },
    },
});



