import Vue from 'vue';
import Router from 'vue-router';
import pagesRouter from '@/web/pages/pagesRouter';
import homepagesRouter from '@/web/homepages/homepagesRouter';
import personalRouter from '@/web/personal/personalRouter';
// import PersonalcenterRouter from '@/web/personalcenter/PersonalcenterRouter';
// import SinglepagesRouter from '@/web/singlepages/SinglepagesRouter';
// import OrderingRouter from '@/web/ordering/OrderingRouter';

Vue.use(Router);

const root = [
    {
        path: "*", // 路径
        redirect: "homepages" //重定向
    }
];

const router = new Router({
    mode: 'hash',
    base: process.env.BASE_URL,
    routes: [...root, ...pagesRouter, ...homepagesRouter,...personalRouter]
});

router.afterEach(() => {
    window.scrollTo(0, 0)
})

export default router



